import { Injectable } from "@angular/core";
import {
  AngularFirestore,
  AngularFirestoreCollection,
  AngularFirestoreDocument,
  AngularFirestoreCollectionGroup,
} from "@angular/fire/firestore";
import { ReturnStatement } from "@angular/compiler";

import { Observable, from } from "rxjs";
import { map } from "rxjs/operators";

import { DocInterface } from "./../models/doc";
import { UserInterface } from "../models/user";

@Injectable({
  providedIn: "root",
})
export class DataApiService {
  private documentosCollection: AngularFirestoreCollection<DocInterface>;
  private documentos: Observable<DocInterface[]>;
  private documentoDoc: AngularFirestoreDocument<DocInterface>;
  private documento: Observable<DocInterface>;
  public selectedDocumento: DocInterface = {
    id: null,
  };
  private utilizadoresCollection: AngularFirestoreCollection<UserInterface>;
  private utilizadores: Observable<UserInterface[]>;
  private userDoc: AngularFirestoreDocument<UserInterface>;
  private user: Observable<UserInterface>;
  public selectedUser: UserInterface = {
    id: null,
  };

  constructor(private afs: AngularFirestore) {
    this.documentosCollection = afs.collection<DocInterface>("docs");
    this.documentos = this.documentosCollection.valueChanges();
    this.utilizadoresCollection = afs.collection<UserInterface>("users");
    this.utilizadores = this.utilizadoresCollection.valueChanges();
  }

  getAllLDocumentos() {
    return (this.documentos = this.documentosCollection.snapshotChanges().pipe(
      map((changes) => {
        return changes.map((action) => {
          const data = action.payload.doc.data() as DocInterface;
          data.id = action.payload.doc.id;
          return data;
        });
      })
    ));
  }
  getOneDocumento(idDocumento: string) {
    this.documentoDoc = this.afs.doc<DocInterface>(`docs/${idDocumento}`);
    return (this.documento = this.documentoDoc.snapshotChanges().pipe(
      map((action) => {
        if (action.payload.exists === false) {
          return null;
        } else {
          const data = action.payload.data() as DocInterface;
          data.id = action.payload.id;
          return data;
        }
      })
    ));
  }
  addDocumento(doc: DocInterface): void {
    this.documentosCollection.add(doc);
  }

  updateDocumento(documento: DocInterface): void {
    let idDocumento = documento.id;
    this.documentoDoc = this.afs.doc<DocInterface>(`docs/${idDocumento}`);
    this.documentoDoc.update(documento);
  }

  deleteDocumento(idDocumento: string): void {
    this.documentoDoc = this.afs.doc<DocInterface>(`docs/${idDocumento}`);
    this.documentoDoc.delete();
  }

  //users

  getAllLUser() {
    return (this.utilizadores = this.utilizadoresCollection
      .snapshotChanges()
      .pipe(
        map((changes) => {
          return changes.map((action) => {
            const data = action.payload.doc.data() as UserInterface;
            data.id = action.payload.doc.id;
            return data;
          });
        })
      ));
  }
  //todo
  addUser(user: UserInterface): void {
    this.utilizadoresCollection.add(user);
  }

  updateUser(user: DocInterface): void {
    let idUser = user.id;
    this.userDoc = this.afs.doc<DocInterface>(`users/${idUser}`);
    this.userDoc.update(user);
  }

  deleteUser(idUser: string): void {
    this.userDoc = this.afs.doc<DocInterface>(`users/${idUser}`);
    this.userDoc.delete();
  }
}
