import { auth } from "firebase/app";
import { AuthService } from "./../../../services/auth.service";
import { Component, OnInit } from "@angular/core";
import { NgForm } from "@angular/forms";

import { DataApiService } from "./../../../services/data-api.service";
import { DocInterface } from "./../../../models/doc";

@Component({
  selector: "app-list",
  templateUrl: "./list.component.html",
  styleUrls: ["./list.component.css"],
})
export class ListComponent implements OnInit {
  public documentos: DocInterface[];
  public isAdmin: any = null;
  public userUid: string = null;

  constructor(
    public dataApi: DataApiService,
    private authService: AuthService
  ) {}

  ngOnInit(): void {
    this.getListDocs();
    this.getCurrentUser();
  }

  getCurrentUser() {
    this.authService.isAuth().subscribe((auth) => {
      if (auth) {
        this.userUid = auth.uid;
        this.authService.isUserAdmin(this.userUid).subscribe((userRole) => {
          console.log("userRole", userRole);
          this.isAdmin = userRole.admin;
          console.log("Admin:", this.isAdmin);
        });
      }
    });
  }

  getListDocs() {
    this.dataApi.getAllLDocumentos().subscribe((docs) => {
      this.documentos = docs;
    });
  }

  onDeleteDoc(idDocumento: string) {
    console.log("DELETE DOC ", idDocumento);
    const confirmDelete = confirm("Tem certeza que deseja apagar?");
    if (confirmDelete) {
      // console.log("deleteConfirm");
      this.dataApi.deleteDocumento(idDocumento);
    }
  }

  onPreUpdateDoc(documento: DocInterface) {
    console.log("DOC ", documento);
    this.dataApi.selectedDocumento = Object.assign({}, documento);
  }
}
