import {
  AngularFirestore,
  AngularFirestoreDocument,
} from "@angular/fire/firestore";
import { Injectable } from "@angular/core";
import { Router } from "@angular/router";
import { AngularFireAuth } from "@angular/fire/auth";

import { UserInterface } from "./../models/user";
import { map } from "rxjs/operators";
import { promise } from "protractor";
import { auth } from "firebase/app";

@Injectable({
  providedIn: "root",
})
export class AuthService {
  constructor(
    public afAuth: AngularFireAuth,
    private afs: AngularFirestore,
    private router: Router
  ) {}

  registerUser(email: string, pass: string, admin: boolean, name: string) {
    return new Promise((res, rej) => {
      this.afAuth
        .createUserWithEmailAndPassword(email, pass)
        .then((userData) => {
          console.log("userdata ", userData);
          res(userData), this.updateUserData(userData.user, admin, name, pass);
        })
        .catch((err) => console.log(rej(err)));
    });
  }

  loginEmailUser(email: string, pass: string) {
    return new Promise((res, rej) => {
      this.afAuth.signInWithEmailAndPassword(email, pass).then(
        (userData) => res(userData),
        (err) => rej(err)
      );
    });
  }
  logoutUser() {
    return this.afAuth.signOut();
  }

  isAuth() {
    return this.afAuth.authState.pipe(map((auth) => auth));
  }

  private updateUserData(user, admin: boolean, name: string, pass: string) {
    const userRef: AngularFirestoreDocument<any> = this.afs.doc(
      `users/${user.uid}`
    );
    const data: UserInterface = {
      id: user.uid,
      email: user.email,
      admin: admin,
      name: name,
    };
    return userRef.set(data, { merge: true });
  }

  isUserAdmin(userUid) {
    return this.afs.doc<UserInterface>(`users/${userUid}`).valueChanges();
  }
}
