import { AuthService } from "./../../../services/auth.service";
import { Router } from "@angular/router";
import { Component, OnInit, ViewChild, ElementRef } from "@angular/core";
import { NgForm } from "@angular/forms";

import { DocInterface } from "./../../../models/doc";
import { DataApiService } from "./../../../services/data-api.service";
import { NodeWithI18n } from "@angular/compiler";
import { resetFakeAsyncZone } from "@angular/core/testing";

@Component({
  selector: "app-modal-new-user",
  templateUrl: "./modal-new-user.component.html",
  styleUrls: ["./modal-new-user.component.css"],
})
export class ModalNewUserComponent implements OnInit {
  @ViewChild("btnClose") btnClose: ElementRef;
  public email: string = "";
  public password: string = "";

  constructor(
    public dataApi: DataApiService,
    private router: Router,
    private authService: AuthService
  ) {}

  ngOnInit(): void {}

  onAddUser(newUserForm: NgForm) {
    this.authService
      .registerUser(
        this.dataApi.selectedUser.email,
        this.dataApi.selectedUser.password,
        this.dataApi.selectedUser.admin,
        this.dataApi.selectedUser.name
      )
      .then((res) => {
        console.log("register ok");
        // this.dataApi.addUser(newUserForm.value);
        console.log("valor ", newUserForm.value);
        newUserForm.resetForm();
        this.btnClose.nativeElement.click();
      });
  }
}
